<template>
  <div
    class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center"
    id="page-login"
  >
    <div class="vx-col sm:w-1/2 md:w-1/2 lg:w-3/4 xl:w-3/5 sm:m-0 m-4">
      <vx-card>
        <div slot="no-body" class="full-page-bg-color">
          <div class="vx-row no-gutter justify-center items-center">
            <div class="vx-col hidden lg:block lg:w-1/2">
              <img
                src="@/assets/images/pages/login.png"
                alt="login"
                class="mx-auto"
              />
            </div>

            <div class="vx-col sm:w-full md:w-full lg:w-1/2 d-theme-dark-bg">
              <div class="p-8 login-tabs-container">
                <div class="vx-card__title mb-4">
                  <h4 class="mb-4">ตรวจสอบสิทธิ์</h4>
                  <p>ยินดีต้อนรับ กรุณาเข้าระบบก่อนการใช้งาน</p>
                </div>

                <div>
                  <vs-input
                    name="email"
                    icon-no-border
                    icon="icon icon-user"
                    icon-pack="feather"
                    label-placeholder="Username"
                    v-model="email"
                    class="w-full"
                  />

                  <vs-input
                    type="password"
                    name="password"
                    icon-no-border
                    icon="icon icon-lock"
                    icon-pack="feather"
                    label-placeholder="Password"
                    v-model="password"
                    class="w-full mt-6"
                    @keyup.enter.native="login"
                  />
                  <div class="flex flex-wrap justify-between my-5">
                    <vs-alert :active="error" color="danger">
                      <span>{{ errorMsg }}</span>
                    </vs-alert>
                  </div>
                  <div class="flex flex-wrap justify-between my-5">
                    <vs-checkbox v-model="checkbox_remember_me" class="mb-3"
                      >จดจำฉันไว้</vs-checkbox
                    >
                    <vs-button class="float-right" @click="login"
                      >เข้าสู่ระบบ</vs-button
                    >
                  </div>
                  <vs-divider>Ver 1.0</vs-divider>
                </div>
              </div>
            </div>
          </div>
        </div>
      </vx-card>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      email: '',
      password: '',
      checkbox_remember_me: false,
      error: false,
      errorMsg: ''
    }
  },
  mounted () {},
  methods: {
    login () {
      this.$http
        .post('/api/login', {
          email: this.email,
          password: this.password
        })
        .then(({ data }) => {
          this.$store.dispatch('setToken', data.data.token).then((data) => {
            this.$store.dispatch('updateDisplayName', data.fullname)
            this.$router.push('/').catch(() => {})
          })
        })
        .catch((err) => {
          this.error = true
          this.password = ''
          this.errorMsg = err.response.data.message[0]
        })
    }
  }
}
</script>

<style lang="scss">
#page-login {
  .social-login-buttons {
    .bg-facebook {
      background-color: #1551b1;
    }
    .bg-twitter {
      background-color: #00aaff;
    }
    .bg-google {
